@import 'https://fonts.googleapis.com/css2?family=Noto+Sans:wght@300;400;500;600;700;900&display=swap';
@import 'https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400;700;900&display=swap';
@import 'https://fonts.googleapis.com/css2?family=Oswald:wght@200;400;700&family=Roboto+Condensed:wght@300;400;700;900&display=swap';
@font-face {
  font-family: "Inter";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts//Inter/Inter-Regular.eot");
  src: url("../fonts//Inter/Inter-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts//Inter/Inter-Regular.woff2") format("woff2"), url("../fonts//Inter/Inter-Regular.woff") format("woff"), url("../fonts//Inter/Inter-Regular.ttf") format("truetype"), url("../fonts//Inter/Inter-Regular.svg#fontawesome") format("svg");
}
@font-face {
  font-family: "Inter";
  font-weight: 600;
  font-style: normal;
  src: url("../fonts//Inter/Inter-Semibold.eot");
  src: url("../fonts//Inter/Inter-Semibold.eot?#iefix") format("embedded-opentype"), url("../fonts//Inter/Inter-Semibold.woff2") format("woff2"), url("../fonts//Inter/Inter-Semibold.woff") format("woff"), url("../fonts//Inter/Inter-Semibold.ttf") format("truetype"), url("../fonts//Inter/Inter-Semibold.svg#fontawesome") format("svg");
}
@font-face {
  font-family: "Inter";
  font-weight: 300;
  font-style: normal;
  src: url("../fonts//Inter/Inter-Light.eot");
  src: url("../fonts//Inter/Inter-Light.eot?#iefix") format("embedded-opentype"), url("../fonts//Inter/Inter-Light.woff2") format("woff2"), url("../fonts//Inter/Inter-Light.woff") format("woff"), url("../fonts//Inter/Inter-Light.ttf") format("truetype"), url("../fonts//Inter/Inter-Light.svg#fontawesome") format("svg");
}
@font-face {
  font-family: "Inter";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts//Inter/Inter-Bold.eot");
  src: url("../fonts//Inter/Inter-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts//Inter/Inter-Bold.woff2") format("woff2"), url("../fonts//Inter/Inter-Bold.woff") format("woff"), url("../fonts//Inter/Inter-Bold.ttf") format("truetype"), url("../fonts//Inter/Inter-Bold.svg#fontawesome") format("svg");
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  padding: 0;
  margin: 0;
  font-family: Noto Sans, Arial, Helvetica, sans-serif;
  font-size: 13px;
  line-height: 18px;
  text-rendering: auto;
}

a {
  color: #0704e1;
  text-decoration: none;
  outline: none;
}

a:hover,
a:focus {
  text-decoration: none;
  color: inherit;
}

p {
  font-family: "Inter";
}

b {
  font-weight: 600;
}

@media (min-width: 1400px) {
  .container {
    max-width: 1650px;
  }
}
a,
a[href^=tel],
a[href^=mailto] {
  color: inherit;
  /* Inherit text color of parent element. */
  text-decoration: none;
  /* Remove underline. */
}

.icon:before {
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  margin-right: 5px;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
ul li {
  padding: 0;
  margin: 0;
}

input,
textarea {
  border: none;
  outline: none;
  font-family: Noto Sans, Arial, Helvetica, sans-serif;
}
input:focus,
textarea:focus {
  outline: none;
}

img {
  width: 100%;
}

.header-nav-container,
.search-bar-container,
.header-banner-info-container {
  max-width: 1280px;
  margin: 0 auto;
  width: 90%;
}
@media (max-width: 480px) {
  .header-nav-container,
  .search-bar-container,
  .header-banner-info-container {
    padding: 40px;
  }
}

.header-nav {
  margin: 8px 0 0 0;
}
.header-nav .header-nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.15rem 0;
}
.header-nav .header-nav-container .header-nav-column.account ul li a {
  font-weight: 500;
}
.header-nav .header-nav-container .header-nav-column ul {
  display: flex;
  gap: 1.5rem;
}
.header-nav .header-nav-container .header-nav-column ul li a {
  color: #000;
  font-size: 0.85rem;
  font-weight: 700;
  text-decoration: none;
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
}
.header-nav .header-nav-container .header-nav-column ul li a:hover {
  color: #4724d8;
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
}
.header-nav .header-nav-container .header-nav-column ul li a.nav-cta {
  background-color: #fe7e00;
  color: #fff;
  border-radius: 50px;
  padding: 0.55rem 1.1rem;
  font-weight: 600;
  border: 2px solid #fe7e00;
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
}
.header-nav .header-nav-container .header-nav-column ul li a.nav-cta:hover {
  background-color: #fff;
  color: #fe7e00;
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
}

.search-bar .search-bar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 0;
}
.search-bar .search-bar-container .search-bar-column {
  display: flex;
  align-items: center;
  gap: 1.25rem;
}
.search-bar .search-bar-container .search-bar-column.search .brand-logo {
  width: 13%;
}
.search-bar .search-bar-container .search-bar-column.search .search-input {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  padding: 0.5rem 0.75rem 0.5rem 1rem;
  width: 52%;
  background: #fff;
  border: 2px solid #0704e1;
  border-radius: 50px;
}
.search-bar .search-bar-container .search-bar-column.search .search-input form {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.search-bar .search-bar-container .search-bar-column.search .search-input form .wide {
  width: calc(50% - 9px);
}
.search-bar .search-bar-container .search-bar-column.search .search-input form .narrow {
  width: calc(50% - 50px);
}
.search-bar .search-bar-container .search-bar-column.search .search-input form .search-separator {
  padding: 0 0.75rem;
  border-left: 1px solid #ddd;
  align-self: normal;
}
.search-bar .search-bar-container .search-bar-column.search .search-input form .search-pin-icon {
  width: 15px;
  margin-right: 15px;
}
.search-bar .search-bar-container .search-bar-column.search .search-input form .submit-cta {
  font-size: 20px;
  color: #0704e1;
}
.search-bar .search-bar-container .search-bar-column.search .view-all a {
  border: 1px solid #ddd;
  color: #888;
  font-size: 0.8rem;
  font-weight: 400;
  border-radius: 50px;
  padding: 0.55rem 1.1rem;
  text-decoration: none;
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
}
.search-bar .search-bar-container .search-bar-column.search .view-all a:hover {
  border: 1px solid #aaa;
  color: #333;
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
}
.search-bar .search-bar-container .search-bar-column.event {
  width: 175px;
}

.header-banner-info .header-banner-info-container {
  display: flex;
  justify-content: space-between;
  padding: 1rem 0 2rem;
}
.header-banner-info .header-banner-info-container .event-info-wrapper, .header-banner-info .header-banner-info-container .work-ads-wrapper .work-ads {
  box-shadow: 0 0 0 0 transparent;
  transition: all 0.3s ease-in-out;
}
.header-banner-info .header-banner-info-container .event-info-wrapper:hover, .header-banner-info .header-banner-info-container .work-ads-wrapper .work-ads:hover {
  box-shadow: 0 0 5px 2px #ddd;
  transition: all 0.3s ease-in-out;
}
.header-banner-info .header-banner-info-container .event-info-wrapper {
  width: 59%;
  display: flex;
  background: linear-gradient(90deg, rgb(18, 8, 225) 0%, rgb(0, 166, 252) 100%);
  align-items: center;
  border-radius: 5px;
  position: relative;
}
.header-banner-info .header-banner-info-container .event-info-wrapper .radial-gradient-element {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: auto;
}
.header-banner-info .header-banner-info-container .event-info-wrapper .event-info-box {
  height: calc(100% - 2rem);
  align-items: center;
  display: flex;
  position: relative;
  z-index: 2;
}
.header-banner-info .header-banner-info-container .event-info-wrapper .event-info-box:not(:last-child) {
  border-right: 1px solid #fff;
}
.header-banner-info .header-banner-info-container .event-info-wrapper .event-countdown {
  font-family: Oswald, Arial, Helvetica, sans-serif;
  width: 37%;
  display: flex;
  flex-direction: row;
  padding: 0 2%;
}
.header-banner-info .header-banner-info-container .event-info-wrapper .event-countdown .countdown-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.25rem;
  width: 2.75rem;
  margin: 0 auto;
}
.header-banner-info .header-banner-info-container .event-info-wrapper .event-countdown .countdown-item span {
  color: #fff;
}
.header-banner-info .header-banner-info-container .event-info-wrapper .event-countdown .countdown-item .digit {
  font-size: 1.85rem;
  font-weight: 700;
  line-height: 2rem;
}
.header-banner-info .header-banner-info-container .event-info-wrapper .event-countdown .countdown-item .text {
  font-size: 0.95rem;
  font-weight: 200;
  text-transform: capitalize;
  line-height: 1rem;
}
.header-banner-info .header-banner-info-container .event-info-wrapper .event-countdown .colon-dots {
  font-family: Noto Sans, Arial, Helvetica, sans-serif;
  display: flex;
  color: #fff;
  font-size: 1.25rem;
  font-weight: 900;
  line-height: 2.5rem;
  align-self: baseline;
}
.header-banner-info .header-banner-info-container .event-info-wrapper .event-org {
  width: 28%;
  padding: 0 1.5rem;
}
.header-banner-info .header-banner-info-container .event-info-wrapper .event-details {
  width: 36%;
  color: #fff;
  padding: 0 0.85rem;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  align-items: flex-start;
  line-height: 1.25rem;
}
.header-banner-info .header-banner-info-container .event-info-wrapper .event-details .date {
  font-size: 0.95rem;
  font-weight: 700;
}
.header-banner-info .header-banner-info-container .event-info-wrapper .event-details .venue {
  font-size: 0.85rem;
  font-weight: 300;
}
.header-banner-info .header-banner-info-container .work-ads-wrapper {
  width: calc(41% - 1rem);
  border-radius: 5px;
  display: flex;
  gap: 1rem;
  max-height: 95px;
}
.header-banner-info .header-banner-info-container .work-ads-wrapper .work-ads {
  width: 50%;
  border-radius: 5px;
  overflow: hidden;
}
.header-banner-info .header-banner-info-container .work-ads-wrapper .work-ads.text-bg {
  background-image: url(https://assets.jobstore.my/images/layouts/header_my_banner.png);
  background-position: 50% 50%;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 2.5rem;
  outline: 1px solid #ddd;
}
.header-banner-info .header-banner-info-container .work-ads-wrapper .work-ads.text-bg h4 {
  text-transform: uppercase;
  font-size: 1.25rem;
  line-height: 1.5rem;
  font-weight: 700;
}
.header-banner-info .header-banner-info-container .work-ads-wrapper .work-ads.image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header-ribbon {
  width: 100%;
  height: 50px;
  line-height: 50px;
  text-align: center;
  background: url("https://assets.jobstore.my/images/header/jobseeker_header_purple_bg.png") #f7f7f7 no-repeat center/cover;
}
.header-ribbon .header-ribbon-container {
  display: flex;
  justify-content: center;
  gap: 5%;
}
.header-ribbon .header-ribbon-container .ribbon-text {
  display: flex;
  gap: 0.75rem;
}
.header-ribbon .header-ribbon-container .ribbon-text h4 {
  color: #fff;
  font-size: 1rem;
}
.header-ribbon .header-ribbon-container .ribbon-text:last-child h4 {
  color: #000;
  font-size: 0.85rem;
  font-weight: 500;
}
.header-ribbon .header-ribbon-container .ribbon-text:last-child h4 span {
  font-weight: 700;
}
.header-ribbon .header-ribbon-container .ribbon-text .ribbon-prompt span {
  background-color: #4724d8;
  padding: 0.05rem 0.7rem;
  font-size: 0.8rem;
  line-height: 20px;
  font-weight: 600;
  color: #fff;
  border: 2px solid #4724d8;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
}
.header-ribbon .header-ribbon-container .ribbon-text .ribbon-prompt span:hover {
  background-color: #fff;
  color: #4724d8;
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
}